body{
  background-color: #1f1e1e;
  color: white;
  overflow: hidden;
}

h1{
  text-align: center;
}

.todo-wrapper{
  background: #353434;
  padding: 3%;
  width: fit-content;
  margin-left:auto;
  margin-right:auto;
  margin-top: 3%;
  max-height: 80vh;
  overflow-y:auto;
  box-shadow: 0px 5px 7px rgb(27, 27, 27);
}


.todo-input{
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(58, 57, 57);
  padding-bottom: 25px;
  margin-bottom: 25 px;
}

.todo-input-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 25px;
}

.todo-input-item label{
  font-weight: bold;
  margin-bottom: 10 px;
}

.todo-input-item input{
  padding: 8 px;
  border: none;
  width: 250 px;
}

.todo-input-item input:focus{
  outline: 2 px solid rgb(0, 230, 122);
}

.primaryBtn{
  background-color: rgb(0, 230, 122);
  color:white;
  border:none;
  border-radius: 0px;
  margin-top: 25px;
  padding: 10px;
  width: 60px;
  cursor: pointer;
}

.primaryBtn:hover{
  background-color: rgb(4, 196, 106);
}

.secondaryBtn{
  background-color: rgb(71, 71, 71);
  color:white;
  border:none;
  border-radius: 0px;
  margin-top: 25px;
  padding: 10px;
  width: fit-content;
  cursor: pointer; 
}


.active{
  background-color: rgb(0, 230, 122)
}

.todo-list{
  display: flex;
  flex-direction: column;

}

.btn-area{
  margin-bottom: 15 px;
}


.todo-list-item{
  background-color : #414040;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding : 25 px;
  padding-bottom: 10 px;
  padding-top: 10px;
  margin-bottom: 10 px;
  box-shadow: 0px 3 px 5px gray;
}

.todo-list-item h3{
  font-size: 25px;
  color:rgb(0, 230, 122);
  font-weight: bold;
  margin: 0;
}

.todo-list-item{
  font-size: 14px;
  color: rgb(161 , 161 , 161);
  margin-top: 8 px;
}

.icon{
  font-size: 35px;
  cursor: pointer;
}

.icon:hover{
  color:red;
}

.check-icon{
  font-size: 25px;
  margin-left: 10px;
  columns: rgb(0,230,122);
}

.check-icon:hover{
  color:rgb(4, 196, 106);

}